/* src/App.css */
.App {
  text-align: center;
  max-width: 800px;
  margin: 0 auto;
  padding: 20px;
}

.App-header {
  margin-bottom: 20px;
}

.scanner-code-container {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-around;
  margin-bottom: 20px;
}

.scanner-code {
  width: 45%;
  margin-bottom: 20px;
}

.thank-you-message,
.date-message {
  margin-bottom: 20px;
}

/* Add responsive styles for mobile devices */
@media screen and (max-width: 600px) {
  .scanner-code-container {
    flex-direction: column;
    align-items: center;
  }

  .scanner-code {
    width: 100%;
  }
}


